<script>
  let cardHeight;
  let cardWidth;

  let elStyle = null;

  // Коэф. смещения. Увеличить при надобности отклонить карточку на больший угол
  const rotationCoefficent = 1;

  const handleMove = (e) => {
    const xVal = e.layerX;
    const yVal = e.layerY;

    //
    const yRotation = rotationCoefficent * ((xVal - cardWidth / 2) / cardWidth);

    const xRotation =
      -rotationCoefficent * ((yVal - cardHeight / 2) / cardHeight);

    const string =
      "perspective(400px) scale(1.02) rotateX(" +
      xRotation +
      "deg) rotateY(" +
      yRotation +
      "deg)";

    elStyle = string;
  };

  const handleMouseOut = () => {
    elStyle = "perspective(500px) scale(1) rotateX(0) rotateY(0)";
  };
</script>

<div
  class="cardWrapper"
  on:mousemove={handleMove}
  on:mouseout={handleMouseOut}
  on:blur={handleMouseOut}
  bind:clientHeight={cardHeight}
  bind:clientWidth={cardWidth}
  style={`
    transform: ${elStyle};
  `}
>
  <slot />
</div>

<style>
  .cardWrapper {
    display: block;
    transition: transform 0.1s;
  }
</style>
