<script>
  import {
    formatNumberWithSpaces,
    formatTimeToMHD,
  } from "../../../utils/utils.svelte";
  import Tooltip from "../../UI/Tooltip/Tooltip.svelte";
  import Bank from "./Bank/Bank.svelte";
  import Corner from "./corners/Corner.svelte";
  import Notch from "./corners/Notch.svelte";
  import HistoryItem from "./HistoryItem/HistoryItem.svelte";
  import Profile from "./Profile/Profile.svelte";
  import RaffleRoulet from "./RaffleRoulet/RaffleRoulet.svelte";
  import Unauth from "./Unauth/Unauth.svelte";

  export let card = {};
  export let cardId = null;

  let isHistoryOpened = false;

  function onHistoryClick() {
    isHistoryOpened = !isHistoryOpened;
  }

  $: isRouletReady = card.current.time_left >= card.current.time_total;

  let startRoulette = false;
  let isGameStarted = false;
  let sum = 0;
</script>

<div class="wrapper">
  <div class="cardHeader">
    <div class="tooltip">
      <Tooltip
        description="Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod"
      />
    </div>
    <div class="logo">
      <img src="images/raffleLogo.svg" alt="logo" />
    </div>
    <div class="prizeInfo">
      <div class="prizeTime">
        <img src="images/alaramIcon.svg" alt="alarm" />
        <span>{formatTimeToMHD(card.current.time_total).hours} ч</span>
      </div>
      <div class="prizeDivider">
        <img src="images/dotIcon.svg" alt="dot" />
      </div>
      <div class="potentialPrize">
        <img src="images/prizeIcon.svg" alt="prize" />
        <span
          >{formatNumberWithSpaces(card.current.min_price)} - {formatNumberWithSpaces(
            card.current.max_price
          )}</span
        >
      </div>
    </div>
  </div>
  <div class="cardBody">
    <Notch
      style={`
        right: -13px;
        top: 0;
      `}
    />
    <Notch
      style={`
        left: -13px;
        top: 0;
        transform: scale(-1, 1) translate(0, -50%);
      `}
    />
    <Bank {isGameStarted} {sum} bind:startRoulette />
    {#if card.current.bet === null}
      <Unauth />
    {:else}
      {#if isRouletReady}
        <RaffleRoulet bind:isGameStarted bind:sum {cardId} {startRoulette} />
      {/if}
      {#if !startRoulette}
        <Profile profile={card.current} />
      {/if}
    {/if}
    <Notch
      style={`
        right: -13px;
        bottom: 0;
        transform: translate(0, 50%);
      `}
    />
    <Notch
      style={`
        left: -13px;
        bottom: 0;
        transform: scale(-1, 1) translate(0, 50%);
      `}
    />
  </div>
  <div class="historyOpen" on:click={onHistoryClick}>
    <div class="historyOpenLabel">
      <span>история билетов</span>
      <img
        style={`
        ${isHistoryOpened ? "transform: scale(1, -1);" : ""}
      `}
        src="images/historyArrowDownIcon.svg"
        alt="historyArrowDownIcon"
      />
    </div>
    <Corner
      style={`
        ${isHistoryOpened ? "display: none;" : ""}
        left: -13px;
      `}
    />
    <Corner
      style={`
        ${isHistoryOpened ? "display: none;" : ""}
        transform: scale(-1, 1);
        right: -13px;
      `}
    />
  </div>
  <div class={`historyBar ${isHistoryOpened ? "" : "historyBarWithBG"}`}>
    <div
      style={`
        visibility: ${isHistoryOpened ? "visible" : "hidden"};
      `}
    >
      {#each card.previous as item}
        <HistoryItem {item} />
      {/each}
    </div>
  </div>
</div>

