    line-height: 16px;
    color: var(--color-white);
  }

  .prizeTime img,
  .potentialPrize img {
    margin-right: 4px;
  }

  .prizeDivider {
    margin: 0 22px;
    display: flex;
  }

  .prizeDivider img {
    width: 5px;
    height: 5px;
  }

  .cardBody {
    position: relative;
    padding: 25px 0;
    height: 501px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-bottom: 2px dashed var(--color-dark-gray);
    border-top: 2px dashed var(--color-dark-gray);
  }

  .historyOpen {
    position: relative;
    height: 56px;
    display: flex;
    align-items: center;
