<script>
  import Carousel from "svelte-carousel";
  import { onMount } from "svelte";

  import CardWrapper from "./CardWrapper/CardWrapper.svelte";
  import RaffleCard from "./RaffleCard/RaffleCard.svelte";

  let wrapperElement = null;

  let showMobileVersion = false;
  const MIN_CONTENT_WIDTH = 1060;

  export let cards = [];

  let slider, prev, next;

  $: prev = slider ? () => slider.goToPrev() : null;
  $: next = slider ? () => slider.goToNext() : null;

  onMount(() => {
    const resizeObserver = new (window.ResizeObserver ||
      window.ResizeObserverP)(([entry]) => {
      const { width } = entry.contentRect;

      if (width < MIN_CONTENT_WIDTH && !showMobileVersion) {
        showMobileVersion = true;

        wrapperElement.style.gridTemplateColumns =
          "repeat(1, minmax(230px, 460px))";
      }
      if (width >= MIN_CONTENT_WIDTH && showMobileVersion) {
        showMobileVersion = false;

        wrapperElement.style.gridTemplateColumns =
          "repeat(3, minmax(320px, 460px))";
      }
    });

    resizeObserver.observe(wrapperElement);
  });
</script>

<section class="wrapper" bind:this={wrapperElement}>
  {#if showMobileVersion}
    <Carousel
      bind:this={slider}
      let:currentPageIndex
      arrows={false}
      infinite={false}
      duration={500}
    >
      <div class="controls" slot="dots">
        <div class="prev" on:click={prev}>
          <img src="images/sliderArrow.svg" alt="goBack" />
        </div>
        <div class="controlsInputRow">
          {#each cards as _, i (i)}
            <input
              type="radio"
              id={i}
              name="slider-radio"
              value={i}
              checked={currentPageIndex == i}
              on:click={() => {
                slider.goTo(i);
              }}
            />
          {/each}
        </div>
        <div class="next" on:click={next}>
          <img
            src="images/sliderArrow.svg"
            style={`
          transform: scale(-1,1);
        `}
            alt="goBack"
          />
        </div>
      </div>
      {#each cards as card, index (index)}
        <div class="slideWrapper">
          <RaffleCard {card} cardId={index} />
        </div>
      {/each}
    </Carousel>
  {:else}
    {#each cards as card, index}
      <CardWrapper>
        <RaffleCard {card} cardId={index} />
      </CardWrapper>
    {/each}
  {/if}
</section>

<style>
  .wrapper {
    position: relative;
    display: grid;
    column-gap: 17px;
    justify-content: center;
    padding-top: 73px;
    grid-template-columns: repeat(3, minmax(320px, 460px));
  }

  .controls {
    position: absolute;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 22px;
  }

  .controlsInputRow {
    margin: 0 28px;
    display: flex;
    align-items: center;
  }

  .controlsInputRow input {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;

    border-radius: 50%;
    width: 8px;
    height: 8px;

    background-color: var(--color-dark-gray);
    transition: 0.2s all linear;
    margin-right: 5px;

    cursor: pointer;
  }

  .controlsInputRow input:checked {
    background-color: var(--color-white);
  }

  .slideWrapper {
    padding: 0 15px;
  }

  .prev,
  .next {
    cursor: pointer;
  }
</style>
