<script>
  export let isGameStarted = false;
  export let sum = 0;
  export let startRoulette = false;

  let localSum = 0;

  const onSumChanged = (duration = 2000) => {
    let startTimestamp = null;

    const step = (timestamp) => {
      if (!startTimestamp) startTimestamp = timestamp;

      const progress = Math.min((timestamp - startTimestamp) / duration, 1);
      localSum = Math.floor(progress * sum);

      if (progress < 1) {
        window.requestAnimationFrame(step);
      }
    };
    window.requestAnimationFrame(step);

    setTimeout(() => {
      startRoulette = true;
    }, 2000);
  };

  $: sum > 0 ? onSumChanged() : null;
</script>

<div class="bank">
  <h4>Банк</h4>
  {#if isGameStarted}
    <div class="bankPublic">
      <img src="images/bankPublicIcon.svg" alt="prize" />
      <span>{localSum}</span>
    </div>
  {:else}
    <div class="bankPrivate">
      <img src="images/bankPrivateIcon.svg" alt="prize" />
      <span>засекречен</span>
    </div>
  {/if}
</div>

