<script>
  export let value = "";
  export let handler;
  export let href = null;
  export let disabled = false;

  const customHandler = () => {
    window.open(href, "_self");
  };
</script>

<button
  {disabled}
  class={$$props.class}
  on:click={href ? customHandler : handler}
  style={$$props.style}
>
  {value}
  <slot />
</button>

<style>
  button {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 18px 23px;

    color: var(--text-color);
    font-weight: 500;
    font-size: 16px;
    line-height: 100%;

    background: var(--bg-color);
    border: none;
    outline: 0;
    border-radius: var(--border-radius-small);
    cursor: pointer;
  }

  button:after {
    content: "";

    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    display: inline-block;

    border-radius: var(--border-radius-small); /*1*/
    border: 2px solid transparent; /*2*/
    background: var(--border-color) border-box; /*3*/
    -webkit-mask: /*4*/ linear-gradient(#fff 0 0) padding-box,
      linear-gradient(#fff 0 0);
    -webkit-mask-composite: xor; /*5'*/
    mask-composite: exclude; /*5*/

    pointer-events: none;
  }

  button:before {
    content: "";
    position: absolute;
    top: 60%;
    left: 50%;
    z-index: -1;

    display: inline-block;
    width: 90%;
    height: 80%;

    border-radius: var(--border-radius-small);
    background: var(--bg-color);
    opacity: 0.8;
    filter: blur(17px);
