<script>
  import { formatNumberWithSpaces } from "../../../../utils/utils.svelte";
  import RankIcon from "../../../common/RankIcon.svelte";
  import Selector from "../../../common/Selector/Selector.svelte";
  import Button from "../../../UI/Button/Button.svelte";
  import AvatarWithBorder from "../AvatarWithBorder/AvatarWithBorder.svelte";

  // test Data
  import avatarsMock from "../../../../avatarsMock.json";
  import winnerMock from "../../../../winnerMock.json";
  import newCurrentMock from "../../../../newCurrentMock.json";
  import { cards } from "../../../../stores/cards";

  export let startRoulette;
  export let isGameStarted = false;
  export let sum = 0;
  export let cardId = null;

  let gameFinished = false;
  let winner = null;

  let wrapperWidth;
  const avatarWidth = 143;
  const gap = 15;

  $: wheelStyle = `
      transform: translateX(-${avatarWidth * 0.5 - gap / 2}px);
    `;

  // Тайминги анимаций
  // Время скролла
  const scrollDuration = 20;
  // Возврат значений после
  const resetAfter = 15;
  // Время анимации банка
  const numberAnimDuration = 2.5;

  const onReset = (current) => {
    gameFinished = false;
    isGameStarted = false;
    sum = 0;
    winner = null;

    cards.update((oldValues) => {
      oldValues[cardId].current = current;

      return oldValues;
    });
  };

  const onGameStart = () => {
    startGame(4000, winnerMock.winner, avatarsMock.avatars);
  };
  const startGame = (_sum, _winner, avatars) => {
    sum = _sum;
    isGameStarted = true;
    winner = _winner;

    // Start game
    setTimeout(() => {
      const index = winner.id;
      const randomize =
        Math.floor(Math.random() * (avatarWidth / 2)) - avatarWidth / 2;
      const landingPosition =
        // -1 потому что начинаем скролл со второго элемента
        (index - 1) * (avatarWidth + gap) + avatarWidth / 2 + randomize;
      wheelStyle = `
        transition: transform ${scrollDuration}s;
        transform: translateX(-${landingPosition}px);
        `;
      // End game
      setTimeout(() => {
        gameFinished = true;
        // Reset
        setTimeout(() => {
          onReset(newCurrentMock.current);
        }, resetAfter * 1000);
      }, scrollDuration * 1000);
    }, numberAnimDuration * 1000);
  };
</script>

<div class="startGame">
  <Button
    disabled={isGameStarted}
    value="Начать игру"
    handler={onGameStart}
    style={`
        --bg-color: var(--gradient-yellow);
        --text-color: var(--color-white);
        --border-color: var(--border-yellow);
      `}
  />
</div>

<div
  class="wrapper"
  bind:clientWidth={wrapperWidth}
  style={!startRoulette
    ? `
  position: fixed;
  visibility: hidden;
  z-index: -1;
`
    : ``}
>
  {#if isGameStarted}
    <div class="roulette">
      <div class="wheel" style={`${wheelStyle}`}>
        {#each avatarsMock.avatars as avatar, index (avatar + index)}
          <AvatarWithBorder imgUrl={avatar} />
        {/each}
      </div>
      <div
        class="rouletteShadows"
        style={`--wrapper-width: ${wrapperWidth}px`}
      />
      <Selector width={wrapperWidth} />
    </div>
  {/if}
  {#if !isGameStarted}
    <div />
  {:else if gameFinished}
    <div class="winnerInfo">
      <div class="user">
        <div class="userRank">
          <RankIcon
            style={`
            --border-radius: 6px;
          `}
            rankId={winner.rank.order}
          />
        </div>
        <span>{winner.username}</span>
      </div>
      <div class="tickets">
        <img src="images/tickets_icon.svg" alt="tickets" />
        <span>{formatNumberWithSpaces(winner.tickets)}</span>
      </div>
    </div>
  {/if}
</div>

