    height: 100%;
    width: calc(100% + 25px);
  }

  .roulette {
    position: relative;
    height: 183px;
    max-width: 456px;
    width: 100%;
    margin: 35px 0 21px 0;
    display: flex;
    align-items: center;
    overflow: hidden;
  }

  .wheel {
    display: flex;
    gap: 15px;
    will-change: transform;
  }

  .rouletteShadows {
    position: absolute;
    left: 50%;
    top: 50%;

    width: 100%;
    height: 100%;

    transform: translate(-50%, -50%);
  }

  .rouletteShadows::after,
  .rouletteShadows::before {
    content: "";
    position: absolute;
    top: 50%;
    transform: translate(0, -50%);

    height: 100%;
    width: calc(var(--wrapper-width) / 2);
    z-index: 5;
  }

  .rouletteShadows::before {
    right: 50%;
    background: linear-gradient(
      270deg,
      rgba(34, 34, 34, 0) 0%,
      rgb(34, 34, 34) 100%
    );
  }

  .rouletteShadows::after {
    left: 50%;
    background: linear-gradient(
      90deg,
      rgba(48, 48, 48, 0) 0%,
      rgb(48, 48, 48) 100%
    );
  }

  .startGame {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .winnerInfo {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .user {
    margin-bottom: 12px;
  }

  .userRank {
    width: 26px;
    height: 26px;
  }

  .user,
  .tickets {
    display: flex;
    align-items: center;
  }

  .user span,
  .tickets span {
