<script context="module">
  export const formatTime = (time, type) => {
    let newTime = time;

    const days = Math.floor(newTime / 86400);
    newTime = newTime - days * 86400;

    const hours = Math.floor(newTime / 3600);
    newTime = newTime - hours * 3600;

    const minutes = Math.floor(newTime / 60);

    switch (type) {
      case "object":
        return {
          days,
          hours,
          minutes,
        };
      case "short":
        return `${days} д : ${hours} ч : ${minutes} м`;
      case "full":
        return `${days} дней : ${hours} часов : ${minutes} минут`;
    }
  };

  const mobileRE =
    /(android|bb\d+|meego).+mobile|armv7l|avantgo|bada\/|blackberry|blazer|compal|elaine|fennec|hiptop|iemobile|ip(hone|od)|iris|kindle|lge |maemo|midp|mmp|mobile.+firefox|netfront|opera m(ob|in)i|palm( os)?|phone|p(ixi|re)\/|plucker|pocket|psp|series[46]0|samsungbrowser|symbian|treo|up\.(browser|link)|vodafone|wap|windows (ce|phone)|xda|xiino/i;
  const notMobileRE = /CrOS/;

  const tabletRE = /android|ipad|playbook|silk/i;

  export const isMobile = (opts) => {
    if (!opts) opts = {};
    let ua = opts.ua;
    if (!ua && typeof navigator !== "undefined") ua = navigator.userAgent;
    if (ua && ua.headers && typeof ua.headers["user-agent"] === "string") {
      ua = ua.headers["user-agent"];
    }
    if (typeof ua !== "string") return false;

    let result =
      (mobileRE.test(ua) && !notMobileRE.test(ua)) ||
      (!!opts.tablet && tabletRE.test(ua));

    if (
      !result &&
      opts.tablet &&
      opts.featureDetect &&
      navigator &&
      navigator.maxTouchPoints > 1 &&
      ua.indexOf("Macintosh") !== -1 &&
      ua.indexOf("Safari") !== -1
    ) {
      result = true;
    }

    return result;
  };

  export const formatNumberWithSpaces = (number) => {
    return number.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ");
  };

  export const formatTimeToMHD = (time) => {
    const dt = new Date(time);

    return {
      mins: dt.getMinutes(),
      days: dt.getDay(),
      hours: dt.getHours(),
    };
  };
</script>
